import { Icon as Iconify } from "@iconify/react";
import { Alert, Card, CardContent, Grid, styled } from "@mui/material";
import DOMPurify from "dompurify";
import Markdown from "marked-react";

import ResponsiveContainer from "../../../../components/ResponsiveContainer";
import { TabView } from "../../../../tabs";
import Form from "./Form";

const MarkdownContainer = styled("div")(({ theme }) => ({
	"& h3": {
		marginTop: 0,
	},
}));

const Page = () => {
	const GUIDE = `
### نکات آموزشی

با افزونه برنده باش هر روز یا هر هفته یا هر ماه یا هر فصل و یار هر 6 ماه بصورت خودکار تعدادی از مشتریان خود را برنده یک تخفیف یا جایزه کوچک کنید ، کافیست تا تعداد برندگان و بازه زمانی اعلام برنده ها را مشخص کنید تا دایرکت بصورت تصادفی هر روز تعدادی برنده از بین مشتریان شما انتخاب کند و متنی که شما مشخص کرده اید برای آنها ارسال کند.

این افزونه با یک متن جذاب و یک هدیه غافلگیر کننده می تواند تاثیر زیادی بر روی فروش شما بگذارد.
`;

	return (
		<TabView
			title="برنده باش"
			Icon={(props) => (
				<Iconify {...props} icon="solar:gift-bold-duotone" />
			)}
		>
			<ResponsiveContainer>
				<Card variant="outlined" sx={{ mb: 3 }}>
					<CardContent sx={{ p: 3 }}>
						<Grid container spacing={5}>
							<Grid item xs={12} sm={6}>
								<Alert color="info">
									<MarkdownContainer>
										<Markdown breaks={true}>
											{DOMPurify.sanitize(GUIDE)}
										</Markdown>
									</MarkdownContainer>
								</Alert>
							</Grid>
							<Grid item xs={12} md={6}>
								<Form />
							</Grid>
						</Grid>
					</CardContent>
				</Card>
			</ResponsiveContainer>
		</TabView>
	);
};

export default Page;
